.gpt3__brand {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  padding: 2rem 4rem;
}

.gpt3__brand div {
  flex: 1;

  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__brand div img {
  max-width: 80px;
  height: 20px;
  margin: 1rem;

  cursor: pointer;
}

@media screen and (max-width: 650px) {
  .gpt3__brand {
    padding: 2rem;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__brand {
    padding: 1rem;
  }

  .gpt3__brand div img {
    width: 60px;
  }
}
