.gpt3__footer {
  background: var(--color-footer);
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;

  padding: 4rem 6rem;
}

.gpt3__footer-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-bottom: 4rem;
}

.gpt3__footer-header h1 {
  text-align: center;
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -2.48px;
}

.gpt3__footer-btn {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1rem;
  margin-bottom: 6rem;
}

.gpt3__footer-btn p {
  border: 1px solid white;
  box-sizing: border-box;
  color: antiquewhite;

  padding: 1rem;
  cursor: pointer;
  border-radius: 5px;
}

.gpt3__footer-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.gpt3__footer-links__logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  max-width: 200px;
  margin-right: 4rem;
  margin-bottom: 4rem;
}

.gpt3__footer-links__logo img {
  margin-bottom: 2rem;
  width: 100px;
  cursor: pointer;
}

.gpt3__footer-links__logo p {
  color: #fff;
  font-family: poppins, sans-serif;
  font-size: 12px;
  font-weight: 400;

  cursor: pointer;
}

.gpt3__footer-links__div {
  display: flex;
  align-items: flex-start;
}

.gpt3__footer-links__div div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;

  margin-right: 2rem;
  margin-left: 2rem;
}

.gpt3__footer-links__div div > h4 {
  color: #fff;
  font-family: poppins;
  font-size: 14px;
  font-weight: 400;

  margin-bottom: 0.5rem;
  cursor: pointer;
}

.gpt3__footer-links__div div > p {
  color: #fff;
  font-size: 12px;
  cursor: pointer;
}

@media screen and (max-width: 1050px) {
  .gpt3__footer-header h1 {
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 0px;
  }

  .gpt3__footer-links__div {
    width: 100%;
    justify-content: space-between;
  }
  .gpt3__footer-links__div div {
    margin-left: 0;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__footer {
    align-items: center;
    padding: 2rem;
  }

  .gpt3__footer-header h1 {
    font-size: 24px;
    line-height: 30px;

    margin-bottom: 0;
  }

  .gpt3__footer-btn p {
    padding: 0.5rem;
    font-size: 10px;
  }

  .gpt3__footer-links__logo img {
    width: 80px;
  }

  .gpt3__footer-links__logo p {
    font-size: 10px;
  }

  .gpt3__footer-links__div div > h4 {
    font-size: 12px;
  }

  .gpt3__footer-links__div div > p {
    font-size: 10px;
  }
}
