* {
  font-family: var(--font-family);
}

.gpt3__header {
  display: flex;
  padding: 4rem 6rem;
}

.gpt3__header-content {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  margin-right: 3rem;
}

.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04rem;
}

.gpt3__header-content p {
  font-family: var(--font-family);
  color: var(--color-text);
  font-size: 20px;
  font-weight: normal;
  line-height: 27px;

  margin-top: 1.5rem;
}

.gpt3__header-content__input {
  display: flex;
  margin: 2rem 0;
}

.gpt3__header-content__input > input {
  font-family: var(--font-family);
  color: var(--color-text);
  background: #052d56;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;

  height: 40px;
  flex: 2;
  min-width: 50px;
  width: 100%;
  flex-shrink: 0;
  border-radius: 5px 0px 0px 5px;
  border: 0;
  outline: none;
  padding: 1.5rem;
}

.gpt3__header-content__input button {
  font-family: var(--font-family);
  color: var(--color-text);
  font-size: 20px;
  font-weight: 700;
  line-height: normal;
  background: #ff4820;
  flex-shrink: 0;

  width: 140px;
  cursor: pointer;
  border-radius: 0px 5px 5px 0px;
  border: 0;
  outline: none;
}

.gpt3__header-content__people {
  display: flex;
  align-items: center;
  justify-content: center;
}

.gpt3__header-content__people img {
  height: 40px;
  flex-shrink: 0;
  width: 180px;
}

.gpt3__header-content__people p {
  font-family: var(--font-family);
  color: #fff;
  font-size: 12px;
  font-weight: 500;
  line-height: 37px;
  margin: 0 0 0 1rem;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gpt3__header-image img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
    padding: 2rem;
  }

  .gpt3__header-content {
    margin: 0 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header {
    margin: 0;
    padding: 0 0 0 1rem;
  }

  .gpt3__header-content {
    margin: 0 0 0 1rem;
  }

  .gpt3__header-content h1 {
    font-size: 50px;
    line-height: 60px;
  }

  .gpt3__header-content p {
    font-size: 15px;
    line-height: 25px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 15px 0;
    font-size: 10px;
  }

  .gpt3__header-content__people img {
    justify-items: flex-start;
    padding: 0;
    margin: 0;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header {
    margin: 0 1rem 0 0;
  }

  .gpt3__header-content {
    margin: 0 0 0 1rem;
  }

  .gpt3__header-content h1 {
    font-size: 28px;
    line-height: 35px;
  }

  .gpt3__header-content p {
    font-size: 15px;
    line-height: 20px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
    justify-content: flex-start;
  }

  .gpt3__header-content__people p {
    font-size: 10px;
    margin: 15px 0;
  }

  .gpt3__header-content__input input {
    font-size: 15px;
  }

  .gpt3__header-content__input button {
    width: 100px;
    font-size: 15px;
  }
}
