.gpt3__cta {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  background: linear-gradient(90deg, #ae67fa 1.84%, #f49867 102.67%);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

  justify-content: space-between;
  align-items: center;

  border: 1px solid #000;
  border-radius: 12px;
  margin: 4rem 6rem;
  padding: 1rem 2rem;
}

.gpt3__cta-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  margin: 0 6rem 0 0;
}

.gpt3__cta-text p {
  color: #0e0e0e;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
}

.gpt3__cta-text h1 {
  color: #000;
  font-family: Manrope;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 45px;
}

.gpt3__cta-button button {
  background: #000;
  color: #fff;
  font-size: 18px;
  font-weight: 400;

  border-radius: 40px;
  height: 45px;
  width: 150px;
  border-style: none;
  cursor: pointer;
}

@media screen and (max-width: 1050px) {
  .gpt3__cta {
    justify-content: flex-start;
  }

  .gpt3__cta-text {
    margin: 0 0 2rem 0;
  }

  .gpt3__cta-text p {
    font-size: 11px;
    line-height: 18px;
  }

  .gpt3__cta-text h1 {
    font-size: 20px;
    line-height: 25px;
  }

  .gpt3__cta-button button {
    font-size: 16px;
    width: 120px;
    height: 40px;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__cta {
    padding: 1rem 2rem;
    margin: 2rem;
  }

  .gpt3__cta-text {
    margin-bottom: 1rem;
  }
}
