.gpt3__whatgpt3 {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 4rem 6rem;
  border-radius: 5px;

  filter: drop-shadow(0 4px 4px rgba(4, 44, 84, 1));
  background: var(--color-footer);
  background: radial-gradient(
    circle at 100% 100%,
    rgba(27, 120, 222, 1) 0%,
    rgba(4, 44, 84, 1) 11%
  );
}

.gpt3__whatgpt3-feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.gpt3__whatgpt3-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin: 4rem 0 2rem;
}

.gpt3__whatgpt3-heading h1 {
  font-family: var(--font-family);
  color: #fff;
  font-weight: 800;
  font-size: 34px;
}

.gpt3__whatgpt3-heading p {
  font-weight: 500;
  cursor: pointer;
  color: var(--color-subtext);
}

.gtp3__whatgpt3-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  margin-top: 2rem;
}

@media screen and (max-width: 1050px) {
  .gpt3__whatgpt3-heading {
    flex-direction: column;
    margin: 0;
    padding: 2rem 0 0 1rem;
    justify-content: flex-start;
    align-items: center;
  }

  .gpt3__whatgpt3-heading h1 {
    margin-bottom: 1.5rem;
    width: 100%;
  }

  .gpt3__whatgpt3-heading p {
    font-size: 16px;
    width: 100%;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__whatgpt3 {
    padding: 3rem;
    margin: 3rem;
  }

  .gpt3__whatgpt3-heading {
    padding: 1.5rem 0 0 0;
    margin-bottom: 2rem;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 34px;
    line-height: 43px;

    margin-bottom: 1rem;
  }

  .gpt3__whatgpt3-heading p {
    font-size: 14px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__whatgpt3 {
    padding: 2rem;
    margin: 2rem;
  }

  .gpt3__whatgpt3-heading {
    padding: 0.8rem 0 0 0;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 24px;
    line-height: 32px;

    margin-bottom: 0.8rem;
  }

  .gpt3__whatgpt3-heading p {
    font-size: 12px;
  }
}
